<template>
  <div>
    <div class="header_top">
      <div class="top_item">
        <img src="../../assets/img/per_fh.png" alt="返回按钮" @click="back">
        <h1>我的积分</h1>
      </div>
      <div>
        <h1>{{integral}}</h1>
      </div>
      <div><span @click="show_intertalMarket">积分商城</span></div>
      <div class="change_tab">
      <router-link tag="div" :to="{name: 'intertalTask'}">积分任务</router-link>
      <router-link tag="div" :to="{name: 'intertalDetail'}">积分明细</router-link>
    </div>
    </div>
    
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <market class="animated" v-if="show_market" v-on:closeMarketModal="closeMarketModal"></market>
    </transition>
  </div>
</template>
<script>
  import market from '@/components/intertalMarket'
  export default {
    components: {
      market
    },
    data() {
      return {
        integral: null,
        show_market: 0
      }
    },
    methods: {
      closeMarketModal(info){
        this.show_market = info;
      },
      show_intertalMarket(){
        this.show_market = 1;
      },
      back() {
        this.$router.push('/mine');
      },
    },
    mounted() {
      this.axios.post('/api/getTotalScore').then(data => {
        this.integral = data.data.result.integral;
      }).catch(err => {
        console.log(err);
      });
    }
  }
</script>
<style lang="less" scoped>
  div.header_top {
    height: 12.5rem;
    background-image: url('../../assets/img/je6yd55m.png');
    background-repeat: no-repeat;
    background-size: cover;
    position: sticky;
    top: 0;
    z-index: 1;
    &>:nth-child(2) {
      width: 100%;
      text-align: center;
      height: 5rem;
      line-height: 5rem;
    }
    &>:nth-child(3) {
      width: 100%;
      text-align: center;
      height: 3.125rem;
      &>span {
        display: inline-block;
        width: 6.25rem;
        height: 2.5rem;
        border: 1px solid #fff;
        color: #fff;
        line-height: 2.5rem;
        border-radius: .625rem;
      }
    }
  }
  .top_item {
    display: flex;
    height: 2.5rem;
    position: relative;
    &>img {
      width: 1.25rem;
      height: 1.875rem;
      position: absolute;
      top: 1.25rem;
      left: 1.25rem;
    }
    &>h1 {
      position: absolute;
      top: .9375rem;
      left: 50%;
      transform: translateX(-50%);
      font-size: 1.25rem;
      height: 2.5rem;
      line-height: 2.5rem;
      font-weight: normal;
    }
  }
  .change_tab {
    display: flex;
    background-color: #fff;
    height: 3.125rem;
    &>div {
      flex: 1;
      height: 3.125rem;
      line-height: 3.125rem;
      text-align: center;
      position: relative;
    }
  }
  div.active {
    color: #0dc04a;
  }
  div.active::after {
    content: '';
    display: block;
    width: 2.1875rem;
    height: .1875rem;
    position: absolute;
    background-color: #0dc04a;
    bottom: 0;
    left: 40%;
    color: #0dc04a;
  }
</style>




